<template>
  <div class="room">
      <el-container>
        <el-aside width="150px">
          <div v-for="item in members" :key="item" style="margin:20px">
            {{ item }}
          </div>
        </el-aside>
        <el-container>
          <el-header>Vue-Chat Room</el-header>
          <el-main>
            <div v-for="item in message" :key='item.admin' style="margin:20px"> 
              <div class="admin">{{ item.admin }}</div>
              <div class="message">{{ item.message }}</div>
            </div>
          </el-main>
          <el-footer height="200px">
            <textarea name="" id="" cols="30" rows="6" v-model="text"></textarea>
            <el-button type="primary" @click="send" style="float:right">发送</el-button>
          </el-footer>
      </el-container>
</el-container>
  </div>
</template>

<script>
import Vue from 'vue'
import store from '../store'
// import socket from 'socket.io'
import VueSocketio from 'vue-socket.io'

Vue.use(VueSocketio,'http://127.0.0.1:3000')

export default {
    name:'room',
    data(){
      return {
        members: [],
        message: [],
        text: '',
      }
    },
    created(){
      // let socket = io('http://localhost')
    },
    mounted(){
         this.$prompt('请输入您的名字', '提示', {
          confirmButtonText: '确定'
        }).then(({ value }) => {
            if(value == ''){
              return
            }
            store.admin = value
            // let time = setInterval(() => {
            //   if(this.$socket){
            //     this.$socket.emit('member',value)
            //     window.clearInterval(time)
            //     return
            //   }
            // },500)
            this.$socket.emit('newmember',{member:value})
        })
    },
    methods:{
      send(){
        let data = {
          admin: store.admin,
          message: this.text
        }
        this.$socket.emit('message', data)
      },
    },
    sockets: {
      connect: function(){
        window.console.log('服务端已连接')
      },
      getmember(res){
        this.members = res
      },
      getmessage(res){
        this.message = res
      }
    }
}
</script>

